<template>
  <v-footer class="GetInTouch">
    <div class="getinTouchBG">
      <img src="../project-img/contacticon.png" alt="Bg">
    </div>
    <div class="form-frame">
      <form @submit.prevent="sendEmail">
          <h3>GET IN TOUCH</h3>
          <v-alert class="successMessage" dense type="success" v-if="senderName"
            >Thank You {{ senderName }}</v-alert
          >
          <v-alert class="successMessage" dense type="error" v-if="formError"
            >Thank You {{ formError }}</v-alert
          >
          <div class="form-inputs">
            <div class="form-group">
              <span
                ><label><h5>Name:</h5></label></span
              >
              <b-form-input
                v-model="name"
                required
                placeholder="Enter your fullname ..."
              />
            </div>
            <div class="form-group">
              <span
                ><label><h5>Email:</h5></label></span
              >
              <b-form-input
                type="email"
                v-model="email"
                required
                placeholder="Enter your email ..."
              />
            </div>
            <div class="form-group">
              <span
                ><label><h5>Phone Number:</h5></label></span
              >
              <VuePhoneNumberInput
                v-model="phoneNumber"
                fetch-country
                :only-countries="['PH']"
                required
              />
            </div>
            <div class="form-group">
              <span
                ><label><h5>Message:</h5></label></span
              >
              <b-textarea
                v-model="message"
                required
                placeholder="Write your message ..."
              />
            </div>
          </div>
          <br />
  
          <v-btn class="send" color="#f5751f" type="submit"
            ><strong>Send</strong></v-btn
          >
          <v-btn class="resetBtn" color="#a8b0b6" @click="reset()"
            ><strong>Reset</strong></v-btn
          >
        </form>
    </div>
    <v-card dark color="#1E56A0" class="myfooter" width="100%">
        <v-card-text class="py-2 white--text text-center align-items-center">
          <div class="icons">
            <a href="https://www.facebook.com/kulokoy999" target="_blank"
              ><img
                class="socialMedia"
                src="../img/socialmedia-icon/facebook.png"
            /></a>
            <a href="https://github.com/christianjavier25/" target="_blank"
              ><img class="socialMedia" src="../img/socialmedia-icon/github.png"
            /></a>
            <a
              href="https://mail.google.com/mail/?view=cm&fs=1&to=javierchristian2512@gmail.com"
              target="_blank"
              ><img
                class="socialMedia"
                src="../img/socialmedia-icon/gmail.png"
                alt=""
            /></a>
          </div>
          <v-divider></v-divider>
          {{ new Date().getFullYear() }} â€” <strong>Christian Javier</strong>
        </v-card-text>
      </v-card>
  </v-footer>
</template>
<script>
import emailjs from "@emailjs/browser";
  import VuePhoneNumberInput from "vue-phone-number-input";
  
  export default {
    name: "contact-footer",
    components: {
      VuePhoneNumberInput,
    },
    data() {
      return {
        name: null,
        senderName: null,
        phoneNumber: null,
        email: null,
        message: null,
        success: null,
        isSuccess: true,
        formError: null,
      };
    },
    methods: {
      sendEmail() {
        if (this.phoneNumber.error == true) {
          this.formError = "Your Phone number is invalid !";
          setTimeout(() => (this.basketAddSuccess = false), 2000);
        } else {
          emailjs
            .send(
              "service_u0h9m6b",
              "template_og50m9j",
              {
                from_name: this.name,
                email_id: this.email,
                contact_number: this.phoneNumber,
                message: this.message,
              },
              "Cxsk023GgFWUdFtEO"
            )
            .then(
              (response) => {
                console.log("SUCCESS!", response.text);
                this.senderName = this.name;
                setTimeout(() => (this.senderName = null), 3000);
                this.name = null;
                this.email = null;
                this.phoneNumber = null;
                this.message = null;
              },
              (error) => {
                console.log("FAILED...", error.text);
              }
            );
        }
      },
      reset() {
        this.senderName = null;
        this.name = null;
        this.email = null;
        this.phoneNumber = null;
        this.message = null;
      },
    },
  };
</script>
<style scoped>
.GetInTouch{
  width: 100%;
  height: 995px;
  background-color: #f6f6f6;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 50px;
}
.form-frame{
  display: flex;
  width: 400px;
  height: 600px;
  padding: 50px;
  background-color: #ffffff;
  border-radius: 20px;
  box-shadow: 0 1px 5px #b3b0b0;
  align-items: center;
  text-align: center;
}
.myfooter{
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
.socialMedia {
    width: 50px;
    margin-left: 20px;
  }
textarea{
  max-height: 100px;
}
.resetBtn {
    margin-left: 50px;
  }
  .resetBtn:hover {
    opacity: 70%;
  }
  .send:hover {
    opacity: 70%;
  }
  .successMessage {
    background-color: rgb(147, 255, 147);
    color: black;
  }
  .form-inputs {
    text-align: left;
  }
  .form-group {
    margin-top: 20px;
  }
  .form-control {
    border: 1px solid;
  }
  .form-control:focus {
    box-shadow: none;
    border-color: black;
  }
  .vue-phone-number-input {
    border: 1px solid;
    border-radius: 5px;
  }
@media only screen and (max-width: 1000px){
  .getinTouchBG img{
    width: 400px;
  }
}
@media only screen and (max-width: 850px){
  .getinTouchBG img{
    width: 300px;
  }
}
@media only screen and (max-width: 765px){
  .getinTouchBG img{
    display: none;
  }
  .GetInTouch{
  justify-content: center;
}
}
@media only screen and (max-width: 665px){
  .GetInTouch{
    padding: 0;
  }
.resetBtn {
    margin-left: 40px;
    width: 50px;
  }
  .send{
    width: 50px;
  }
  b, strong{
    font-size: 12px;
  }
}
</style>